import React, {useState, useEffect} from 'react';
import './App.css';
import NewsArticle from './NewsArticle';
import Logo from './Images/Logo.png';


function App() {
  const apiKey = '8d1235d5fe3042d19d1cd214d77a69d4';
const [searchInput, setSearchInput] = useState('');
  const [category, setCategory] = useState('general');
  const [search, setSearch] = useState('');
const [articles, setArticles] = useState([]);
const [loading, setLoading] = useState(true);

useEffect(() => {
  getNews();
  console.log('effect has run');
},[category, search]);

const handleSubmit = (e) => {
  e.preventDefault();
setSearch(searchInput);
}

const getNews = async () => {
  setLoading(true)
  try {
    const response = await fetch(`https://newsapi.org/v2/top-headlines?country=gb&category=${category}&q=${search}&apiKey=${apiKey}`)
    const data = await response.json();
if(data.articles){
  setArticles(data.articles);
 setLoading(false);
} else {
setArticles([]);
}
setLoading(false)
      

  } catch (error) {
    setLoading(false)
  }
}



  return (
    <div className="main">

      <div className="header">
        <div className="logo-container">
<img className="logo" src={Logo} alt="image" />
<h1>Tempest News</h1>
</div>
      <div className="category">
    

    <button className="category-btn" onClick={() => setCategory('general')}>General</button>

    <button className="category-btn" onClick={() => setCategory('business')}>Business</button>

    <button className="category-btn" onClick={() => setCategory('entertainment')}>Entertainment</button>

    <button className="category-btn" onClick={() => setCategory('health')}>Health</button>

    <button className="category-btn" onClick={() => setCategory('science')}>Science</button>

    <button className="category-btn" onClick={() => setCategory('sports')}>Sports</button>

    <button className="category-btn" onClick={() => setCategory('technology')}>Technology</button>

      </div>
      </div>
      <form className="search-form" onSubmit={handleSubmit}>
        <input 
        className="search-bar" 
        type="text"
        value={searchInput}
        onChange={(e) => setSearchInput(e.target.value)}
        />
        <button className="search-btn" type="submit">
          Search
          </button>
      </form>
      {loading && <h1>Loading</h1>}
      <div className="grid-container">
{!loading && articles.map(item =>(
  <NewsArticle 
  title={item.title} 
  img={item.urlToImage}
  author={item.author}
  url={item.url}
  source={item.source.name}
  />
  
))}
</div>
    </div>
    
  );
}

export default App;
